// src/api/fetchCheckUserCompanyInfo.ts
export async function fetchCheckUserCompanyInfo(
  accessToken: string,
): Promise<boolean> {
  if (!accessToken) throw new Error('トークンがありません');

  const response = await fetch(
    'https://aichan-container-apps.agreeablehill-a0330681.japaneast.azurecontainerapps.io/api/check_user_company?',
    {
      method: 'POST',
      headers: {
        Authorization: `Bearer ${accessToken}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({}),
    },
  );

  if (!response.ok) {
    const text = await response.text();
    throw new Error(text);
  }

  const data = await response.json();
  return data.status;
}
