import React, { useEffect, useRef, useState, useCallback } from 'react';
import MarkdownRender from '../components/MarkdownRender';
import { TypewriterWithCursor } from '../components/TypewriterWithCursor';

type ChatRoom = {
  room_id: number;
  room_name: string;
  created_at: string;
  updated_at: string;
};

type ChatMessage = {
  id: string;
  room_id: number;
  sender: 'user' | 'assistant';
  text: string;
  created_at: string;
};

type ChatPanelProps = {
  selectedRoom: ChatRoom | null;
  userEmail: string;
  aiTyping: boolean;
  setAITyping: (flag: boolean) => void;
  messages: ChatMessage[];
  setMessages: React.Dispatch<React.SetStateAction<ChatMessage[]>>;
  WEBAPIS_ENDPOINT: string;
  accessToken: string;
  fetchChatRooms: () => Promise<void>;
};

function sleep(ms: number) {
  return new Promise((res) => setTimeout(res, ms));
}
async function* typeWriter(text: string, speed = 15) {
  let cur = '';
  for (let i = 0; i < text.length; i++) {
    cur += text[i];
    yield cur;
    await sleep(speed);
  }
}
function isDefaultRoomName(roomName: string) {
  return /^ルーム\d+$/.test(roomName) || /^ルーム\d{5,}$/.test(roomName);
}
function getConfirmedMarkdown(text: string): [string, string] {
  const codeMatch = text.match(/```/g);
  if (codeMatch && codeMatch.length % 2 === 1) {
    const lastOpen = text.lastIndexOf('```');
    if (lastOpen >= 0) {
      return [text.slice(0, lastOpen), text.slice(lastOpen)];
    }
  }
  return [text, ''];
}

const TypingLoader: React.FC = () => {
  const [dots, setDots] = useState('');
  useEffect(() => {
    const id = setInterval(() => {
      setDots((dots) => (dots.length >= 3 ? '' : dots + '.'));
    }, 400);
    return () => clearInterval(id);
  }, []);
  return (
    <span className="text-blue-400 pl-1 animate-pulse">{dots || '.'}</span>
  );
};

export default function ChatPanel({
  selectedRoom,
  userEmail,
  aiTyping,
  setAITyping,
  messages,
  setMessages,
  WEBAPIS_ENDPOINT,
  accessToken,
  fetchChatRooms,
}: ChatPanelProps) {
  const [input, setInput] = useState('');
  const [typingMessageId, setTypingMessageId] = useState<string | null>(null);
  const [typingContent, setTypingContent] = useState('');
  const [typingStarted, setTypingStarted] = useState(false);

  const messagesEndRef = useRef<HTMLDivElement>(null);
  const scrollRef = useRef<HTMLDivElement>(null);
  const isSending = useRef(false);

  // --- スクロール監視 ---
  const [isAtBottom, setIsAtBottom] = useState(true);
  const autoScrollRef = useRef(true);

  // チャット履歴取得: aiTyping中は絶対fetchしない
  useEffect(() => {
    if (!selectedRoom?.room_id || aiTyping) return;
    const fetchChatHistory = async (roomId: number) => {
      try {
        const res = await fetch(
          'https://aichan-container-apps.agreeablehill-a0330681.japaneast.azurecontainerapps.io/api/get_chats?',
          {
            method: 'POST',
            headers: {
              Authorization: `Bearer ${accessToken}`,
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({ room_id: roomId }),
          },
        );
        const data = await res.json();
        if (data.error) {
          setMessages([
            {
              id: 'err',
              room_id: roomId,
              sender: 'assistant',
              text: data.error,
              created_at: new Date().toISOString(),
            },
          ]);
          return;
        }
        const history: ChatMessage[] = (data.chat_logs || []).map(
          // eslint-disable-next-line @typescript-eslint/no-explicit-any
          (log: any, idx: number) => ({
            id: `history-${idx}`,
            room_id: log.room_id,
            sender: log.speaker === 2 ? 'user' : 'assistant',
            text: log.chat_history,
            created_at: log.created_at,
          }),
        );
        setMessages(history);
      } catch {
        setMessages([
          {
            id: 'err',
            room_id: roomId,
            sender: 'assistant',
            text: 'チャット履歴の取得に失敗しました',
            created_at: new Date().toISOString(),
          },
        ]);
      }
    };
    fetchChatHistory(selectedRoom.room_id);
    // eslint-disable-next-line
  }, [selectedRoom, aiTyping, accessToken]);

  // スクロール監視
  const handleScroll = useCallback(() => {
    const box = scrollRef.current;
    if (!box) return;
    const atBottom =
      Math.abs(box.scrollHeight - box.scrollTop - box.clientHeight) < 30;
    setIsAtBottom(atBottom);

    // 下にいないなら自動スクロール解除
    if (!atBottom && autoScrollRef.current) {
      autoScrollRef.current = false;
    }
    // 最下部に戻ったら自動スクロール再開
    if (atBottom && !autoScrollRef.current) {
      autoScrollRef.current = true;
    }
  }, []);

  useEffect(() => {
    const box = scrollRef.current;
    if (!box) return;
    box.addEventListener('scroll', handleScroll);
    // 初期表示は一番下
    setTimeout(() => {
      box.scrollTop = box.scrollHeight;
      setIsAtBottom(true);
      autoScrollRef.current = true;
    }, 0);
    return () => {
      box.removeEventListener('scroll', handleScroll);
    };
  }, [handleScroll]);

  // メッセージ追加時「autoScrollRef.current===true」なら下に自動スクロール
  useEffect(() => {
    const box = scrollRef.current;
    if (!box) return;
    if (autoScrollRef.current) {
      setTimeout(() => {
        box.scrollTop = box.scrollHeight;
      }, 10);
    }
  }, [messages, aiTyping, typingContent]);

  // ルーム名自動変更API
  const updateChatRoomName = async (room_id: number, newName: string) => {
    const SET_ROOMNAME_ENDPOINT =
      'https://aichan-container-apps.agreeablehill-a0330681.japaneast.azurecontainerapps.io/api/set_chatroom?';
    await fetch(SET_ROOMNAME_ENDPOINT, {
      method: 'POST',
      headers: {
        Authorization: `Bearer ${accessToken}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ room_id, room_name: newName }),
    });
    await fetchChatRooms();
  };

  // ユーザーが送信した瞬間だけAI応答する
  const handleSend = async () => {
    if (!input.trim() || !selectedRoom || isSending.current) return;
    isSending.current = true;
    const msgId = Math.random().toString(36).slice(2);

    // 1. ユーザー発言
    const userMsg: ChatMessage = {
      id: msgId + '-user',
      room_id: selectedRoom.room_id,
      sender: 'user',
      text: input,
      created_at: new Date().toISOString(),
    };
    setMessages((msgs) => [...msgs, userMsg]);
    setInput('');
    setAITyping(true);
    setTypingStarted(false);

    // 下に自動スクロール復活
    setTimeout(() => {
      autoScrollRef.current = true;
      setIsAtBottom(true);
      const box = scrollRef.current;
      if (box) box.scrollTop = box.scrollHeight;
    }, 0);

    // デフォルト名のルームかどうか判定用
    const isDefaultName = isDefaultRoomName(selectedRoom.room_name);
    let didUpdateName = false;

    try {
      // 2. AI応答API
      const aiRes = await fetch(WEBAPIS_ENDPOINT, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          room_id: String(selectedRoom.room_id),
          message: userMsg.text,
          user_id: userEmail || 'test_user',
          model: '12',
          temperature: 0.7,
          language: 'ja',
        }),
      });
      const apiResult = await aiRes.json();
      const aiMsgText =
        apiResult?.choices?.[0]?.message?.content || '（AI返答なし）';

      if (isDefaultName && messages.length === 0 && input.trim()) {
        const newName = input.trim().slice(0, 10);
        await updateChatRoomName(selectedRoom.room_id, newName);
        didUpdateName = true;
      }

      // タイピングアニメーション＋Markdown即時切替
      const aiMessageId = msgId + '-ai';
      setTypingMessageId(aiMessageId);
      setTypingStarted(false);

      // --- ローディングアニメーション（AI応答前の「・・・」用） ---
      await new Promise((res) => setTimeout(res, 700));
      setTypingStarted(true);

      let cur = '';
      for await (const next of typeWriter(aiMsgText, 18)) {
        cur = next;
        setTypingContent(cur);

        setMessages((msgs) => {
          const existIdx = msgs.findIndex((m) => m.id === aiMessageId);
          if (existIdx >= 0) {
            return msgs.map((m, i) =>
              i === existIdx ? { ...m, text: cur } : m,
            );
          } else {
            return [
              ...msgs,
              {
                id: aiMessageId,
                room_id: selectedRoom.room_id,
                sender: 'assistant',
                text: cur,
                created_at: new Date().toISOString(),
              },
            ];
          }
        });
      }
      setTypingMessageId(null);
      setTypingContent('');
      setTypingStarted(false);
      setAITyping(false);
      isSending.current = false;

      if (didUpdateName) {
        await fetchChatRooms();
      }
    } catch (e) {
      setMessages((msgs) => [
        ...msgs,
        {
          id: msgId + '-err',
          room_id: selectedRoom.room_id,
          sender: 'assistant',
          text: 'AI応答エラー: ' + (e instanceof Error ? e.message : String(e)),
          created_at: new Date().toISOString(),
        },
      ]);
      setAITyping(false);
      setTypingMessageId(null);
      setTypingContent('');
      isSending.current = false;
    }
  };

  // ★ユーザー吹き出しの直後に「・・・」アニメーションを必ず1つだけ表示
  function renderMessagesWithAILoader() {
    const rendered = [];
    for (let i = 0; i < messages.length; i++) {
      const m = messages[i];
      const isTypingMsg =
        aiTyping &&
        typingMessageId === m.id &&
        m.sender === 'assistant' &&
        i === messages.length - 1;

      let confirmedMd = '';
      let typingRest = '';
      if (isTypingMsg && typingContent) {
        [confirmedMd, typingRest] = getConfirmedMarkdown(typingContent);
      }

      rendered.push(
        <div
          key={m.id}
          className={`mb-2 flex ${
            m.sender === 'user' ? 'justify-end' : 'justify-start'
          }`}
        >
          <div
            className={`max-w-xl px-4 py-2 rounded-xl shadow text-base whitespace-pre-line ${
              m.sender === 'user'
                ? 'bg-blue-500 text-white'
                : 'bg-gray-100 text-black'
            }`}
            style={{ fontFamily: 'inherit', wordBreak: 'break-word' }}
          >
            {/* タイピング中AIメッセージのみ部分Markdown描画 */}
            {isTypingMsg ? (
              typingStarted ? (
                <div style={{ whiteSpace: 'pre-line', display: 'inline' }}>
                  {confirmedMd && (
                    <MarkdownRender>{confirmedMd}</MarkdownRender>
                  )}
                  <TypewriterWithCursor text={typingRest} />
                </div>
              ) : (
                <span className="pl-1">
                  <TypingLoader />
                </span>
              )
            ) : m.sender === 'assistant' ? (
              <MarkdownRender>{m.text}</MarkdownRender>
            ) : (
              m.text
            )}
          </div>
        </div>,
      );

      // ユーザーの直後でAI返答待ちなら、「・・・」ローダー吹き出しを1つ追加
      const isLastUser =
        m.sender === 'user' &&
        i === messages.length - 1 &&
        aiTyping &&
        !typingStarted;

      if (isLastUser) {
        rendered.push(
          <div key={'ai-loader'} className="mb-2 flex justify-start">
            <div
              className="max-w-xl px-4 py-2 rounded-xl shadow text-base bg-gray-100 text-black"
              style={{
                fontFamily: 'inherit',
                wordBreak: 'break-word',
              }}
            >
              <TypingLoader />
            </div>
          </div>,
        );
      }
    }
    return rendered;
  }

  return (
    <div className="flex-1 flex flex-col bg-white relative">
      {/* 入力欄 */}
      <form
        className="flex border-t p-4 bg-gray-50"
        onSubmit={(e) => {
          e.preventDefault();
          handleSend();
        }}
      >
        <input
          type="text"
          className="flex-1 px-4 py-2 rounded-l border outline-none"
          value={input}
          placeholder="メッセージを入力..."
          disabled={aiTyping || !selectedRoom}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={(e) => {
            if (e.key === 'Enter' && !e.shiftKey && !aiTyping) {
              e.preventDefault();
              handleSend();
            }
          }}
        />
        <button
          type="submit"
          className="bg-blue-500 text-white px-6 rounded-r disabled:opacity-50"
          disabled={!input.trim() || aiTyping || !selectedRoom}
        >
          送信
        </button>
      </form>
      {/* チャット履歴表示 */}
      <div
        ref={scrollRef}
        className="flex-1 p-6 overflow-auto"
        style={{
          minHeight: 0,
          position: 'relative',
          background: 'white',
        }}
      >
        {renderMessagesWithAILoader()}
        <div ref={messagesEndRef}></div>
        {/* ↓↓↓ 右下ボタン（下まで行ってない時だけ）↓↓↓ */}
        {!isAtBottom && (
          <button
            type="button"
            className="fixed z-30 bottom-24 right-12 md:bottom-10 md:right-10 p-3 rounded-full shadow-lg bg-blue-500 text-white hover:bg-blue-600 transition"
            style={{
              fontSize: 22,
              boxShadow: '0 2px 8px rgba(0,0,0,0.2)',
            }}
            onClick={() => {
              const box = scrollRef.current;
              if (box) {
                box.scrollTo({ top: box.scrollHeight, behavior: 'smooth' });
                autoScrollRef.current = true;
                setIsAtBottom(true);
              }
            }}
            aria-label="最下部へスクロール"
          >
            ↓
          </button>
        )}
      </div>
    </div>
  );
}
