import { useEffect, useState } from 'react';
import Header from './object/header';
import LoadingScreen from './components/ui/LoadingScreen';
import ChatPanel from './object/ChatPanel'; // 別ファイル
import { AuthLog } from './lib/authLogger';

const API_BASE =
  'https://aichan-container-apps.agreeablehill-a0330681.japaneast.azurecontainerapps.io/api';
const WEBAPIS_ENDPOINT =
  'https://aichan-webapis.azurewebsites.net/api/send_messages?';
const SET_ROOMNAME_ENDPOINT =
  'https://aichan-container-apps.agreeablehill-a0330681.japaneast.azurecontainerapps.io/api/set_chatroom?';

type ChatRoom = {
  room_id: number;
  room_name: string;
  created_at: string;
  updated_at: string;
};

type ChatMessage = {
  id: string;
  room_id: number;
  sender: 'user' | 'assistant';
  text: string;
  created_at: string;
};

type MainScreenProps = {
  logout: () => void;
  userData: {
    name: string;
    email: string;
    department_name: string;
  };
  companyStatus: boolean;
  accessToken: string;
  authLogs: AuthLog[];
  downloadLogs: () => void;
};

export default function MainScreen({
  logout,
  userData,
  companyStatus,
  accessToken,
}: MainScreenProps) {
  const [loading, setLoading] = useState(true);
  const [chatRooms, setChatRooms] = useState<ChatRoom[]>([]);
  const [selectedRoom, setSelectedRoom] = useState<ChatRoom | null>(null);
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [aiTyping, setAITyping] = useState(false);

  // ルーム名編集モーダル用
  const [editNameModal, setEditNameModal] = useState(false);
  const [editNameValue, setEditNameValue] = useState('');
  const [editingRoom, setEditingRoom] = useState<ChatRoom | null>(null);

  // チャットルーム一覧取得
  const fetchChatRooms = async () => {
    setLoading(true);
    const response = await fetch(`${API_BASE}/get_chatrooms?`, {
      method: 'POST',
      headers: {
        Authorization: `Bearer ${accessToken}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({}),
    });
    const data = await response.json();
    if (data.rooms) {
      setChatRooms(data.rooms);
      if (data.rooms.length > 0) {
        setSelectedRoom(data.rooms[0]);
      } else {
        await createAndSelectNewRoom();
      }
    }
    setLoading(false);
  };

  // 新規チャットルーム作成
  const createAndSelectNewRoom = async () => {
    const now = new Date();
    const roomName = `ルーム${now.getHours()}${now.getMinutes()}${now.getSeconds()}`;
    await fetch(`${API_BASE}/register_chatroom?`, {
      method: 'POST',
      headers: {
        Authorization: `Bearer ${accessToken}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ room_name: roomName }),
    });
    // 新規作成後、リスト再取得して自動選択
    const response = await fetch(`${API_BASE}/get_chatrooms?`, {
      method: 'POST',
      headers: {
        Authorization: `Bearer ${accessToken}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({}),
    });
    const data = await response.json();
    setChatRooms(data.rooms);
    const last = data.rooms.find((r: ChatRoom) => r.room_name === roomName);
    if (last) setSelectedRoom(last);
  };

  // ルーム名編集API
  const updateChatRoomName = async (room_id: number, newName: string) => {
    await fetch(SET_ROOMNAME_ENDPOINT, {
      method: 'POST',
      headers: {
        Authorization: `Bearer ${accessToken}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ room_id, room_name: newName }),
    });
    setEditNameModal(false);
    setEditNameValue('');
    setEditingRoom(null);
    await fetchChatRooms();
  };

  // 初回のみチャットルーム取得
  useEffect(() => {
    fetchChatRooms();
    // eslint-disable-next-line
  }, []);

  if (loading) return <LoadingScreen loadingFlag={1} />;

  return (
    <div className="flex flex-col h-screen">
      <Header
        onLogout={logout}
        onToggleTranscript={() => {}}
        userStatus={userData?.name ?? ''}
        companyStatus={companyStatus}
      />
      {/* 認証ログダウンロード 
      <div className="p-2 bg-gray-100 border-b flex items-center gap-2">
        <span className="font-bold text-sm">認証ログ</span>
        <button
          className="px-2 py-1 rounded bg-blue-400 text-white hover:bg-blue-500 text-xs"
          onClick={downloadLogs}
        >
          ログをダウンロード
        </button>
        <pre className="flex-1 text-xs bg-black text-green-400 p-2 rounded max-h-24 overflow-auto ml-2">
          {authLogs
            .map(
              (l) =>
                `[${l.timestamp}] [${l.step}] ${l.detail}${
                  l.data ? '\n' + JSON.stringify(l.data, null, 2) : ''
                }`,
            )
            .join('\n\n')}
        </pre>
      </div>*/}
      <div className="flex flex-1 h-screen overflow-hidden">
        {/* サイドバー：チャットルーム */}
        <aside className="w-64 border-r bg-gray-50 flex flex-col">
          <div className="px-4 py-3 border-b font-bold">チャットルーム</div>
          <div className="flex-1 overflow-auto">
            {chatRooms.map((room) => (
              <div key={room.room_id} className="flex items-center border-b">
                <button
                  className={`flex-1 text-left px-4 py-2 hover:bg-blue-100 ${selectedRoom?.room_id === room.room_id ? 'bg-blue-200 font-bold' : ''}`}
                  onClick={() => setSelectedRoom(room)}
                >
                  {room.room_name}
                </button>
                {/* 編集ボタン */}
                <button
                  className="px-2 py-1 text-xs text-blue-600 hover:underline"
                  title="名前変更"
                  onClick={() => {
                    setEditingRoom(room);
                    setEditNameValue(room.room_name);
                    setEditNameModal(true);
                  }}
                >
                  編集
                </button>
              </div>
            ))}
          </div>
          <div className="p-2 border-t">
            <button
              className="w-full px-3 py-1 bg-blue-500 text-white rounded hover:bg-blue-600"
              onClick={createAndSelectNewRoom}
            >
              ＋ 新しいルーム
            </button>
          </div>
        </aside>

        {/* チャットパネル本体 */}
        <ChatPanel
          selectedRoom={selectedRoom}
          userEmail={userData.email}
          aiTyping={aiTyping}
          setAITyping={setAITyping}
          messages={messages}
          setMessages={setMessages}
          WEBAPIS_ENDPOINT={WEBAPIS_ENDPOINT}
          accessToken={accessToken}
          fetchChatRooms={fetchChatRooms}
        />
      </div>
      {/* ルーム名編集モーダル */}
      {editNameModal && editingRoom && (
        <div className="fixed z-50 inset-0 bg-black bg-opacity-40 flex items-center justify-center">
          <div className="bg-white rounded-xl p-6 shadow-xl min-w-[320px] flex flex-col gap-3">
            <div className="font-bold mb-1">ルーム名を編集</div>
            <input
              className="border p-2 rounded"
              value={editNameValue}
              onChange={(e) => setEditNameValue(e.target.value)}
              maxLength={20}
              autoFocus
            />
            <div className="flex justify-end gap-2">
              <button
                className="px-4 py-1 rounded bg-gray-300 hover:bg-gray-400"
                onClick={() => setEditNameModal(false)}
              >
                キャンセル
              </button>
              <button
                className="px-4 py-1 rounded bg-blue-500 text-white hover:bg-blue-600"
                disabled={!editNameValue.trim()}
                onClick={() =>
                  updateChatRoomName(editingRoom.room_id, editNameValue.trim())
                }
              >
                保存
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
