// components/TypewriterWithCursor.tsx
import React, { useEffect, useState } from 'react';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';

type Props = {
  text: string; // 最終的な出力内容
  isMarkdown?: boolean; // Markdownとして描画するか
  speed?: number; // タイピング速度
  onDone?: () => void; // タイピング終了時に呼ぶコールバック
};

export const TypewriterWithCursor: React.FC<Props> = ({
  text,
  isMarkdown = true,
  speed = 40,
  onDone,
}) => {
  const [display, setDisplay] = useState('');
  const [showCursor, setShowCursor] = useState(true);
  const [done, setDone] = useState(false);

  useEffect(() => {
    let i = 0;
    setDisplay('');
    setDone(false);

    const interval = setInterval(() => {
      setDisplay(text.slice(0, i + 1));
      i++;
      if (i >= text.length) {
        clearInterval(interval);
        setDone(true);
        if (onDone) onDone();
      }
    }, speed);

    return () => clearInterval(interval);
  }, [text, speed, onDone]);

  useEffect(() => {
    if (!done) {
      const timer = setInterval(() => setShowCursor((v) => !v), 500);
      return () => clearInterval(timer);
    }
  }, [done]);

  return (
    <span className="inline-block align-bottom">
      {isMarkdown ? (
        <ReactMarkdown remarkPlugins={[remarkGfm]}>{display}</ReactMarkdown>
      ) : (
        display
      )}
      {!done && (
        <span
          className="text-blue-500 animate-blink"
          style={{ opacity: showCursor ? 1 : 0, marginLeft: 0 }}
        >
          |
        </span>
      )}
    </span>
  );
};
